@import url(https://fonts.googleapis.com/css2?family=Catamaran:wght@100;200;300;400;500;600;700;800;900&family=Lato:wght@100;300;400;700;900&family=Poppins:ital,wght@0,100;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&display=swap);
*,
*::after,
*::before {
  /* font-size: 1.1rem; */
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  cursor: default;
}
::-webkit-scrollbar {
  /* width: 0px; */
}
/* font import ............................... */
