/* open sans */

:root {
  /* colors  */
  --black-clr: #000000;
  --white-clr: #ffffff;
  --gray-clr: #f2f2f2;
  --gray-mid-clr: #b4b4b4;
  --gray-dark-clr: #969696;
  --gray-darkest-clr: #4d5a62;

  --primary-clr: #1dd3b0;
  --primary-clr2: #49bdb5;
  --primary-light-clr: #49bdb5;
  --primary-dark-clr: #49bdb5;

  --secondary-clr: #02a4ff;
  --secondary-dark-clr: #016cb4;

  --green-clr: #b9ff47;
}
body {
  font-size: 1.05rem;
  font-family: "Lato", sans-serif;
}
a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}
ul {
  list-style: none;
}
img,
video {
  object-fit: cover;
}
h1,
h2 {
  font-family: "catamaran", sans-serif;
  font-weight: 600;
  word-spacing: 0px;
  line-height: 1;
  /* margin-bottom: 0.5rem; */
}
.headline-1 {
  font-size: 4.5rem;
  margin-bottom: 2rem;
}
.headline-2 {
  color: #4d5a62;
  color: var(--gray-darkest-clr);
  font-size: 2.3rem;
  padding-block: 2.5rem 1.5rem;
}
.headline-3 {
  color: #495057;
  font-size: 28px;
}
.headline-4 {
  color: #495057;
  font-size: 28px;
  font-weight: 600;
}
.padding-top-87 {
  margin-top: 5.5vmax;
}
.bg-gray {
  background-color: rgba(248, 248, 248, 70%);
  background-color: #f5f5f5;
}
.text-center {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.row {
  width: 100%;
  overflow: hidden;
}
.jumbotron {
  max-width: 1100px;
  margin-inline: auto;
  padding: 4rem 1.5rem;
  background-color: #b9ff47;
  background-color: var(--green-clr);
  margin-block: 1rem;
  border-radius: 15px;
}
.jumbotron .headline-1 {
  font-size: 4rem;
  margin: 2rem;
  color: #4d5a62;
  color: var(--gray-darkest-clr);
}
.jumbotron .btn-secondary {
  background-color: #4d5a62;
  background-color: var(--gray-darkest-clr);
}
.headline-span {
  display: inline-block;
  margin-inline: 0.45rem;
  color: #1dd3b0;
  color: var(--primary-clr);
  text-decoration: underline;
}
.lead-1 {
  margin-block: 1.7rem;
  font-family: "lato", sans-serif;
}
.lead-2 {
  margin-block: 1.4rem;
  font-family: "lato", sans-serif;
  font-weight: 400;
  line-height: 1.3;
  font-size: 1.3rem;
  max-width: 1100px;
  color: #7f7f7f;
}
.lead-3 {
  margin-block: 3.2rem;
  font-family: "lato", sans-serif;
  font-size: 1.06rem;
  text-align: left;
  color: #4d5a62;
  color: var(--gray-darkest-clr);
}
.btn {
  margin-block: 1.5rem;
  display: inline-block;
  margin-right: 0.8rem;
  padding: 0.7rem 1rem;
  text-align: center;
  border: none;
  border-radius: 5px;
  font-family: "open sans", sans-serif;
  font-weight: 600;
  min-width: 175px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 25%);
}
.btn-primary {
  background-color: rgb(207 207 207 / 30%);
}
.btn-secondary {
  background-color: #1dd3b0;
  background-color: var(--primary-clr);
  color: #ffffff;
  color: var(--white-clr);
}
.flex-wrapper {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}

.flex-Uy79i {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 2rem;
  gap: 2rem;
}
.flex-Uy79i > .iou,
.flex-Uy79i > .iou + ul {
  flex: 0 1 45%;
}
.flex-Uy79i > .iou + ul {
  margin-left: auto;
}

/* header wrapper start */
.header-wrapper {
  padding: 1.5rem 0.2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  font-family: "open sans";
  font-weight: 600;
  color: var(--gray-darkest-clr);
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);

  margin-inline: auto;
}

.navlink-list {
  display: flex;
}
.navlink-list-item {
  padding: 0.5rem 1rem;
  margin-inline: 0.7rem;
  /* background-color: gray; */
  border-radius: 10px;
  transition: 0.3s;
}
li.navlink-list-item {
  display: grid;
  place-content: center;
}
.navlink-list-item:hover {
  background-color: rgba(16, 16, 16, 10%);
  transition: 0.5s;
}
/* header wrapper end */

/* hero wrapper start */
.hero-wrapper {
  padding: 1rem;
  /* background-color: #f5f5f5; */
  display: flex;
  align-items: flex-end;
}
.hero-item {
  /* flex: 1 1 50%; */
}
.hero-item .lead {
  max-width: 450px;
  line-height: 1.3;
}
.hero-item:nth-child(1) {
  align-self: center;
}
.hero-img {
  position: relative;
  left: 7%;
  transform: translateX(-15%);
}
.hero-model {
  position: relative;
  top: 40px;
  object-fit: contain;
  width: 475px;
  height: 100%;
}
.svg-1 {
  position: absolute;
  left: -15%;
  z-index: -1;
}
.svg-2 {
  position: absolute;
  top: -20%;
  right: -21%;
  z-index: -1;
}
/* hero wrapper end */

/* accociative wrapper  */
.associative-wrapper {
  text-align: center;
  padding: 1rem;
}
.associative_items {
  display: flex;
  justify-content: space-evenly;
  grid-gap: 1rem;
  gap: 1rem;
}
.associtaive_item {
  padding-block: 2rem;
  object-fit: contain;
  transform: scale(0.93);
}
/* accociative wrapper  */

/* desc wrapper  */
.desc_card-wrapper {
  grid-gap: 1.5 rem;
  gap: 1.5 rem;
}
.desc_card {
  text-align: left;
  width: 300px;
  height: 401px;
  background-color: var(--white-clr);
  border-radius: 15px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 20%);

  display: grid;
  grid-template-columns: 1fr;
  /* grid-template-rows: 1fr 0.4fr 0.6fr; */
  grid-template-rows: 120px;
  padding: 1rem;
  transition: 0.3s;
}
.desc_card:hover {
  background-color: var(--green-clr);
  box-shadow: 0 6px 2px 0 rgba(0, 0, 0, 20%);
}
.desc_card .lead-3 {
  color: var(--gray-dark-clr);
}

/* desc wrapper  */

/* program wrapper  */
.program_items {
  max-width: 1096px;
  flex-wrap: wrap;
  padding-block: 2rem;
  grid-gap: 2rem;
  gap: 2rem;
  /* height: 100vh; */
}
.program_card {
  flex: 1 0 47%;
  display: flex;
  flex-wrap: wrap;
  padding: 1rem 1.3rem;
  background-color: #f5f5f5;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 20%);
  border-radius: 15px;
}
.program_card > * {
  flex: 0 1 100%;
}
.program_card .lead-3 {
  font-size: 1.3rem !important;
}
.program_card .headline-3 {
  background-color: var(--green-clr);
  padding: 0.7rem 1.3rem;
  box-shadow: 0 4px 4px var(--primary-clr);
  border-radius: 5px;
  margin-bottom: 1rem;
}
.program_card-main-info {
  display: grid;
  grid-template-columns: 0.6fr 1fr;
  grid-template-rows: 1fr;
  grid-gap: 0.5rem;
}
.program_card-main-info > * {
  background-color: #e2e2e2;
  border-radius: 5px;
}
.program_card-main-info-item-name {
  color: #086375;
  font-weight: 400;
}
.program_card-main-info-item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 1rem;
  text-align: left;
}
ul.program_card-main-details {
  list-style: disc;
  margin-inline: 0.4rem;
  text-align: left;
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  padding: 1rem;
  padding-top: 2.8rem;

  color: #495057;
  font-size: 19px;
}
/* program wrapper  */

/* carrier wrapper  */
.carrier-card-wrapper {
  justify-content: space-evenly;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}
.carrier-card .headline-3 {
  text-align: center;
}
.carrier-card {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
  width: 300px;
  /* border: 1px red solid; */
}

.carrier-card-img {
  text-align: center;
}
.carrier-card-list {
  word-spacing: 0.5px;
  padding: 1rem;
  list-style: unset;
  color: #757575;
  display: flex;
  flex-direction: column;
  grid-gap: 0.3rem;
  gap: 0.3rem;
  font-size: 17px;
}
/* carrier wrapper  */

/* about program wrapper  */
.about-program-items {
  max-width: 1000px;
  justify-content: center;
  margin: auto;
  margin-block: 1rem 3rem;
}
.about-program-items > * {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 19ch;
}
.about-program-items > *:nth-of-type(1) {
  flex: 1 1 100%;
}
.about-program-items .lead-1 {
  margin: 0;
  /* padding: 0; */
}
/* about program wrapper  */

/* .custom table wrapper  */
table.custom_table {
  width: 100%;
  text-align: left;
  max-width: 1000px;
  margin: auto;
  border-collapse: collapse;
  overflow: hidden;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
table.custom_table tr {
  height: 40px;
}
th {
  padding: 0.4rem;
}
td {
  display: flex;
  justify-content: center;
}
.custom_table-header {
  background-color: var(--primary-clr);
  color: var(--white-clr);
  padding: 0.4rem;
  text-align: center;
}
.custom_table-main .custom_table-headline {
  color: #495057;
  padding-left: 3rem;
}
.custom_table-main:nth-child(even) th {
  background-color: #f8f8f8;
}
.custom_table-main:nth-child(odd) th {
  background-color: #e2e2e2;
}
.custom_table-data {
  text-align: center;
}
th.custom_table-data {
  padding: 0;
  position: relative;
}
th.custom_table-data > * {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;

  display: grid;
  place-content: center;
}
.custom_table-main img {
  transform: scale(0.8);
}
.custom_table-main:nth-child(even) .custom_table-trueValue {
  background: #e5faf5;
}
.custom_table-main:nth-child(odd) .custom_table-trueValue {
  background: #c6f4eb;
}
.custom_table-main:nth-child(even) .custom_table-falseValue {
  background: #eeffd6;
}
.custom_table-main:nth-child(odd) .custom_table-falseValue {
  background: #e0ffad;
}
/* .custom table wrapper  */

/* testimonials wrapper  */
.testimonial_card {
  width: 480px;
  height: 678px;
  padding: 2rem;
  background: linear-gradient(
    180deg,
    var(--primary-clr) 61.5%,
    var(--green-clr) 40%
  );
  border-radius: 15px;
  box-shadow: 0 8px 4px rgba(0, 0, 0, 25%);
}
.testimonial_card-info {
  font-size: 24px;
  color: var(--white-clr);
  min-height: 300px;
}

.testimonial_card-user {
  font-family: "Catamaran", sans-serif;
  text-align: center;
}
.testimonial_card-user-name {
  font-weight: 700;
  position: relative;
  top: 5px;
}
.testimonial_card-user-img {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  object-fit: cover;
}
.testimonial_card-user-affiiated_company-img {
  margin-top: 1rem;
}
.Testimonial-items {
  grid-gap: 1.5rem;
  gap: 1.5rem;
  flex-wrap: wrap;
  justify-content: center;
}
/* testimonials wrapper  */

.container {
  padding: 2rem 0;
}

/* for pc....................................... */

@media only screen and (min-width: 1400px) {
  .container {
    max-width: 1200px;
    margin-inline: auto;
    /* background-color: rosybrown !important; */
  }
}
/* for laptop....................................... */
@media only screen and (max-width: 1399px) {
  .container {
    max-width: 1200px;
    margin-inline: auto;

    /* background-color: royalblue !important; */
  }
}
/* for tablet....................................... */
@media only screen and (max-width: 768px) {
  .container {
    max-width: 100%;
    margin-inline: 0;
    padding: 1rem;
    /* background-color: rebeccapurple !important; */
  }
  .flex-wrapper {
    flex-wrap: wrap;
    justify-content: center;
  }
  a.btn {
    margin-block: 0.4rem 0 !important;
    margin-right: 0 !important;
  }
  .associative_items {
    flex-wrap: wrap;
  }
  .container.hero-wrapper.padding-top-87 {
    flex-direction: column-reverse;
    align-items: center;
  }
  .hero-item.hero-img {
    transform: scale(0.8);
  }
  .program_card-main-actions {
    grid-gap: 0.5rem;
    gap: 0.5rem;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  .flex-Uy79i > .iou,
  .flex-Uy79i > .iou + ul {
    flex: 0 1 100%;
  }
  h1.headline-1 {
    font-size: 2.4rem;
  }
  h1.headline-1.text-center {
    margin: 0;
    font-size: 3rem;
    margin-bottom: 0.7rem;
  }
  .jumbotron {
    border-radius: 0;
  }
  .hero-item:nth-child(1) {
    align-self: center;
    margin-top: -20rem;
    position: relative;
    z-index: 1;
    background: rgba(255, 255, 255, 0.8);
    padding: 1rem 1.5rem;
    border-radius: 15px;
    -webkit-backdrop-filter: blur(3px);
            backdrop-filter: blur(3px);
  }
}
/* for mobile....................................... */
@media only screen and (max-width: 480px) {
  .container {
    max-width: 100%;
    /* background-color: red !important; */
  }
}

